// ==========================================================
// Fonts
// ==========================================================

@font-face {
  font-family: 'Metropolis';
  src: url('https://static.juntoseguros.com/fonts/Metropolis/Metropolis-Regular.woff2')
      format('woff2'),
    url('https://static.juntoseguros.com/fonts/Metropolis/Metropolis-Regular.woff')
      format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Metropolis';
  src: url('https://static.juntoseguros.com/fonts/Metropolis/Metropolis-SemiBold.woff2')
      format('woff2'),
    url('https://static.juntoseguros.com/fonts/Metropolis/Metropolis-SemiBold.woff')
      format('woff');
  font-weight: 600;
  font-style: normal;
}

$font-family-metropolis: Metropolis, Helvetica Neue, Helvetica, Arial,
  sans-serif;

